import React, { Component, Fragment } from 'react';
import { Card, Row, Table, Col } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { ColEditDelete } from 'components/common';
import { factorOfTrainingScore } from 'dump/data';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DAO_TAO__THAM_SO__NHAP_DU_LIEU;
class FactorOfTraningScore extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.title = 'Hệ số rèn luyện';
    this.urlOfBtnEdit = '#/factorOfTrainingScore/form/edit/';
    this.columns = [
      {
        title: 'Tên quy chế',
        dataIndex: 'regulation',
        key: 'regulation',
      },
      {
        title: 'Mã tham số',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Tên tham số',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Giá trị',
        dataIndex: 'value',
        key: 'value',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          factorOfTrainingScore.length >= 1 ? (
            <Fragment>
              <ColEditDelete
                linkEdit={{
                  to: {
                    pathname: ROUTER_FORM_DATA,
                    search: `?id=${record.code}`,
                  },
                }}
                onConfirmDelete={() => this.handleDelete(record.id)}
              />
            </Fragment>
          ) : null,
      },
    ];
  }

  render() {
    return (
      <Card title={this.title}>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={factorOfTrainingScore} columns={this.columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <FactorOfTraningScore />
  </Page>
);



